.kyc-outter {
  display: flex;
  flex: 1 1;
  width: 100%;
  min-height: 100vh;
  height: inherit;
  height: auto;
  overflow: hidden;
}
.kyc-outter .kyc-children {
  display: flex;
  flex-direction: column;
  height: calc(45vh);
  padding: 50px 50px 56px;
  overflow: auto;
}
.kyc-outter .kyc-wrapper {
  flex: 1 1;
  height: calc(45vh);
  overflow: auto;
}
.kyc-outter .kyc-wrapper.kyc-sign .kyc-children {
  padding-bottom: 0px;
}
.kyc-outter .kyc-wrapper.kyc-intro {
  height: 100vh;
}
.kyc-outter .kyc-sidebar-outer {
  position: relative;
  height: 100vh;
  width: 308px;
}
.kyc-outter .kyc-sidebar-outer.kyc-sidebar-outer-hidden {
  width: 0;
}
.kyc-outter .kyc-sidebar {
  background: #0053FA;
  width: 308px;
  display: flex;
  flex-direction: column;
  padding: 10% 40px;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
  justify-content: center;
  align-items: center;
}
.kyc-outter .kyc-sidebar.kyc-sidebar-visible {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.kyc-outter .kyc-sidebar.kyc-sidebar-hidden {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}
.kyc-outter .kyc-sidebar-item {
  display: flex;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;
  color: #DFDFDF;
  letter-spacing: 0;
  min-height: 20%;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-title {
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  margin-bottom: 10px;
  color: #FFFFFF;
  letter-spacing: 0;
  opacity: 0.5;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-title span {
  margin-right: 10px;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-title svg {
  opacity: 0.5;
  vertical-align: top;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-description {
  opacity: 0;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-index {
  position: static;
  width: 25px;
  height: 25px;
  line-height: 24px;
  text-align: center;
  color: white;
  font-weight: 500;
  font-size: 15px;
  letter-spacing: 0;
  border-radius: 100%;
  margin-right: 15px;
  border: 1px solid #FFF;
}
.kyc-outter .kyc-sidebar-item .kyc-sidebar-item-index:before {
  content: ' ';
  width: 1px;
  height: calc(76%);
  position: absolute;
  background: white;
  left: 12px;
  margin-top: 24px;
  opacity: 0.4;
}
.kyc-outter .kyc-sidebar-item:last-child .kyc-sidebar-item-index:before {
  content: none;
}
.kyc-outter .kyc-sidebar-item.active .kyc-sidebar-item-title,
.kyc-outter .kyc-sidebar-item.done .kyc-sidebar-item-title {
  opacity: 1;
}
.kyc-outter .kyc-sidebar-item.active .kyc-sidebar-item-description,
.kyc-outter .kyc-sidebar-item.done .kyc-sidebar-item-description {
  opacity: 1;
}
.kyc-outter .kyc-sidebar-item.active .kyc-sidebar-item-index {
  color: #0053FA;
  background: #FFFFFF;
  font-weight: 500;
  border: 1px solid #002D89;
  box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.27);
}
.kyc-outter .kyc-sidebar-item.done .kyc-sidebar-item-index {
  color: #0053FA;
  background: #FFFFFF;
  font-weight: 500;
}
.kyc-outter .kyc-sidebar-item.done .kyc-sidebar-item-index:before {
  opacity: 1;
}
.kyc-outter .kyc-subtitle {
  font-weight: 300;
  font-size: 25px;
  color: #000000;
  letter-spacing: 0;
  line-height: 40px;
}
.kyc-outter .kyc-hint-title {
  margin: 10px 0 15px;
  font-weight: 700;
  font-size: 11px;
  color: #0053FA;
  letter-spacing: 0;
  text-decoration: underline;
  text-transform: uppercase;
}
.kyc-outter .kyc-hint-description {
  font-weight: 400;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
  line-height: 21px;
}
.quality-assurance-dialog {
  width: inherit !important;
  max-width: 80vw;
  max-height: 90vh;
  top: 5vh;
  overflow: hidden;
}
.quality-assurance-dialog .ant-modal-content,
.quality-assurance-dialog .ant-modal-body,
.quality-assurance-dialog .body-dialog,
.quality-assurance-dialog .quality-assurance-preview {
  height: 100%;
}
.quality-assurance-dialog.ant-modal.dialog .ant-modal-body {
  padding: 14px;
}
.quality-assurance-dialog .quality-assurance-preview {
  display: flex;
  width: 100%;
}
.quality-assurance-dialog .quality-assurance-preview .quality-assurance-form {
  flex: 1 1;
  height: 100%;
}
.quality-assurance-dialog .quality-assurance-preview .preview-content {
  height: 100%;
  max-width: 50%;
}
.quality-assurance-dialog .quality-assurance-preview .preview-content canvas,
.quality-assurance-dialog .quality-assurance-preview .preview-content img {
  height: 100%;
}
.quality-assurance-dialog .quality-assurance-preview .quality-assurance-title {
  font-weight: 500;
  color: #0d1b2a;
  letter-spacing: 0;
  line-height: 27px;
  margin-bottom: 10%;
  font-size: 16px;
}
.quality-assurance-dialog .quality-assurance-preview .ant-btn {
  display: inline-block;
  margin-right: 10px !important;
}

